  border: calc(1px * var(--font-size)) solid #c9c9c9;
  background: #f5f6f6;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.field::placeholder {
  color: #4f4f4d;
  font-weight: 300;
}

.field:disabled {
  opacity: 0.7;
}

@media (max-width: 959.98px) {
  .field {
    --size: 3/4;
    --font-size: 15/17;
  }
}

.blockquote {
  --size: 1;
  --font-size: 1;
  text-align: left;
  text-align: start;
  color: #444242;
  font-size: calc(20px * var(--font-size));
  line-height: 1.25;
  font-family: "DIN Pro", sans-serif;
  padding: 0 calc(60px * var(--size));
}

.blockquote q {
  quotes: none;
  display: block;
  position: relative;
}

.blockquote q::before, .blockquote q::after {
  content: '';
  width: calc(46px * var(--size));
  height: calc(40px * var(--size));
  position: absolute;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  pointer-events: none;
}

.blockquote q::before {
  top: 0;
  left: calc(-60px * var(--size));
}

.blockquote q::after {
  right: calc(-60px * var(--size));
  bottom: 0;
  transform: rotate(180deg);
}

.blockquote cite {
  font-size: calc(28px * var(--font-size));
  line-height: 1.15;
  display: block;
  margin-top: calc(30px * var(--font-size));
}

.blockquote cite small {
  font-style: normal;
  font-size: calc(18px * var(--font-size));
}

@media (max-width: 959.98px) {
  .blockquote {
    --size: 3/4;
    --font-size: 15/17;
  }
}
